import '@servicenow/now-loader';
import { getTile } from './getTile';
import { t } from 'sn-translate';

export const getStateData = (type, widget) => {
	let icon = getTile(type);
	let headerText = t('No content available.');
	let bodyText = t(
		'Contact your ServiceNow administrator for more information.'
	);

	if (type === 'loading') {
		icon = <now-loader />;
		headerText = t('Loading');
		bodyText = t('Loading{0} Data', widget ? ` ${widget}` : '');
	} else if (type === 'loading-error') {
		headerText = t('This content could not be loaded.');
	} else if (type === 'permission-error') {
		headerText = t('You do not have permission to view this data.');
	} else if (type === 'not-supported') {
		headerText = t('Your Workspace does not support this visualization.');
	} else if (type === 'not-configured') {
		headerText = t('This has not been setup.');
	} else if (type === 'no-resources') {
		headerText = t('No resources available.');
		bodyText = t('Contact your ServiceNow administrator to set these up.');
	}

	return {
		icon,
		headerText,
		bodyText
	};
};
