import { t } from 'sn-translate';
import noDataTile from './assets/no-data-1-sm.svg';
import interruptedTile from './assets/interrupted-1-sm.svg';
import errorTile from './assets/error-1-sm.svg';
import permissionsTile from './assets/permissions-1-sm.svg';
import notConfiguredTile from './assets/unconfigured-1-sm.svg';

const fileMap = {
	'loading-error': interruptedTile,
	'permission-error': permissionsTile,
	'not-supported': interruptedTile,
	'not-configured': notConfiguredTile,
	'no-resources': errorTile,
	empty: noDataTile
};

export const getTile = type => {
	return (
		<img
			src={fileMap[type]}
			className="tile-image"
			alt={t('Empty Container')}
		/>
	);
};
