import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-icon';
import styles from './styles.scss';
import { getStateData } from './getStateData';
import { getIcon } from './getIcon';

const withFallback = (prop, fallback) => {
	if (prop === undefined) {
		return fallback;
	}

	return prop || undefined;
};

const view = ({ properties }) => {
	const { type, widget, variant } = properties;

	const defaultStateData = getStateData(type, widget);
	const icon = withFallback(getIcon(properties.icon), defaultStateData.icon);
	const headerText = withFallback(
		properties.headerText,
		defaultStateData.headerText
	);
	const bodyText = withFallback(properties.bodyText, defaultStateData.bodyText);

	return (
		<div
			class={{
				'empty-state': true,
				[`variant-${variant.toLowerCase()}`]: variant
			}}
		>
			{icon && <div className="icon">{icon}</div>}
			{headerText && <div className="header">{headerText}</div>}
			{bodyText && <div className="body">{bodyText}</div>}
		</div>
	);
};

/**
 * Displays an icon and a message informing the user why there is no available
 * data.
 *
 * ```jsx
 * <now-empty-state type="permission-error" variant="small" />
 * ```
 *
 * @summary Displays an icon and a message informing the user why there is no available
 * data.
 * @seismicElement now-empty-state
 */
createCustomElement('now-empty-state', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		/**
		 * Types provide a default configuration for specifying the icon and text
		 * that is displayed, based on a given situtation:
		 *
		 * - empty: there is no available data.
		 * - loading: data is being fetched and is not available yet.
		 * - loading-error: data could not be loaded, e.g.: due to network failure.
		 * - permission-error: user does not have permission to view the data.
		 * - not-supported: the application does not support this type of data.
		 * - not-configured: the data has not yet been configured.
		 * - no-resources: no resources are available.
		 *
		 * @type {"empty"|"loading"|"loading-error"|"permission-error"|"not-supported"|"not-configured"|"no-resources"}
		 */
		type: {
			default: 'empty',
			schema: {
				type: 'string',
				enum: [
					'empty',
					'loading',
					'loading-error',
					'permission-error',
					'not-supported',
					'not-configured',
					'no-resources'
				]
			}
		},

		/**
		 * What type of data would be shown here if there were data. Used to
		 * customize the messaging for some states.
		 *
		 * @type {string}
		 */
		widget: { default: '', schema: { type: 'string' } },

		/**
		 * Controls the vertical size and padding of the component.
		 *
		 * - fit: the component is centered within its container.
		 * - auto: the component is sized automatically based on the text content.
		 * - small: the component is rendered at a fixed, small size
		 * - medium: the component is rendered at a fixed, medium size
		 * - large: the component is rendered at a fixed, large size
		 *
		 * @type {"fit"|"auto"|"small"|"medium"|"large"}
		 */
		variant: {
			default: 'fit',
			schema: {
				type: 'string',
				enum: ['fit', 'auto', 'small', 'medium', 'large']
			}
		},

		/**
		 * Override the icon that is displayed. If not provided, the icon will
		 * be determined automatically based on the `type`
		 *
		 * Must be the name of an icon provided by the [design-system icon
		 * library](https://emotive.servicenow.com/plan/icon-gallery), or
		 * set to `false` to render no icon.
		 *
		 * @type {string|false}
		 */
		icon: {
			schema: {
				oneOf: [
					{
						type: 'string'
					},
					{
						type: 'boolean',
						const: false
					}
				]
			}
		},

		/**
		 * Override the header text that is displayed. If not provided, the text
		 * will be determined automatically based on the `type`. Set to `false`
		 * to render no header text.
		 *
		 * @type {string|false}
		 */
		headerText: {
			schema: {
				oneOf: [
					{
						type: 'string'
					},
					{
						type: 'boolean',
						const: false
					}
				]
			}
		},

		/**
		 * Override the body text that is displayed. If not provided, the text
		 * will be determined automatically based on the `type`. Set to `false`
		 * to render no body text.
		 *
		 * @type {string|false}
		 */
		bodyText: {
			schema: {
				oneOf: [
					{
						type: 'string'
					},
					{
						type: 'boolean',
						const: false
					}
				]
			}
		}
	},
	styles
});

export default 'now-empty-state';
